@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:wght@400;600&display=swap);
:root {
  --body-side-margin: 9rem;
}

body {
  margin: 0;
  padding: 0rem 9rem;
  padding: 0rem var(--body-side-margin);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input, button, submit { border:none; } 

button, input[type="submit"], input[type="reset"] {
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

button {
  all: unset;
  color: blue;
  -webkit-text-fill-color: blue;
  cursor: pointer;
}

a{
  text-decoration: none;
}

button:focus {
  outline: orange 5px auto;
}

h1{
  font-family: 'EB Garamond', serif;
}

h2{
  font-family: 'Work Sans', sans-serif;
}

h3{
  font-family: 'EB Garamond', serif;
}

h4{
  font-family: 'Work Sans', sans-serif;
  font-weight: 700;
  font-size: 0.95rem;
}

p{
  font-family: 'Work Sans', sans-serif;
}



@media only screen and (max-width: 428px){
  :root {
    --body-side-margin: 2rem;
  }

}
.header-container{
  display: flex;
  width: 100%;
  top: 0;
  left: 0;
  border-bottom: 1px solid #000000;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0px;
}

.header-options-container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.header-button-dropdown{
  padding: 0 1.5rem;
}

.header-button{
  border: 2px solid #000000;
  margin-left: 1rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: #000000;
  text-decoration: none;
  transition: 0.25 linear;
}

.header-button-font{
  margin: 0;
  padding: 0.65rem 1rem;
  color: #000000;
  text-decoration: none;
}

.header-button:hover .header-button-font{
  color: rgba(0, 0, 0, 0.55);
  transition: 0.25s;
}

.header-button:hover{
  border-color: rgba(0, 0, 0, 0.55);
  transition: 0.25s;
}

@media only screen and (max-width: 428px){

  .header-container{
    flex-direction: column;

  }
  
.header-options-container{
  flex-direction: column;
}
}
.footer-container{
  background-color: #000000;
  position: relative;
  width: 100vw;
  bottom: 0;
  left: calc(var(--body-side-margin) * -1);
  padding: 2rem var(--body-side-margin);
  box-sizing: border-box;
}

.footer-text-container{
  display: flex;
  flex-direction: row;
}

.footer-text-left{
  color: rgba(255,255,255,0.5);
  margin-right: 2rem;
  min-width: 120px;
  margin: 0.75rem 0;
}

.footer-text-right{
  color: #FFF;
  margin: 0.75rem 0;
  margin-right: 2rem;
}

.footer-bottom-container{
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin: 2rem 0 1rem 0;
}

.footer-bottom-text-container{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.footer-bottom-text{
  color: #FFF;
  margin: 0 1.5rem;
  font-size: 0.9rem;
}

.footer-icon{
  width: 24px;
  height: 24px;
}

.footer-bottom-linkedin{
  width: 24px;
  height: 24px;
  margin-right: 25px;
}

.footer-bottom-instagram{
  width: 24px;
  height: 24px;
  border-radius: 4px;
  margin-right: 25px;
}

.footer-bottom-facebook{
  width: 24px;
  height: 24px;
  border-radius: 4px;
  margin-right: 25px;
}


@media only screen and (max-width: 428px){
  .footer-bottom-text-container{
    flex-direction: column;
  }

  .footer-bottom-container{
    flex-direction: column;
    align-items: center;
  }
  .footer-bottom-text-container{
    margin-bottom: 15px;
  }
  .team-member-name{
    align-items: center;
  }
  .footer-bottom-facebook{
    margin-right: 0;
  }
}

.learn-more-container{
   border-top: 1.5px solid rgba(0,0,0,0.15);
   display: flex;
   justify-content: space-between;
   align-items: center;
   margin-bottom: 3rem;
}

.learn-more-next{
  margin-right: 6px;
  color: rgba(0,0,0,0.5);
  font-family: 'Work Sans', sans-serif;
  font-size: 1.1rem;
}

.learn-more-title{
  font-family: 'EB Garamond', serif;
  font-size: 1.3rem;
  margin-bottom: 1.25rem;
}

.learn-more-description{
  width: 60%;
  font-weight: 400;
  font-size: 1rem;
  margin-top: 0;
}

.learn-more-link{
  text-decoration: none;
  color: #000;
  width: 140px;
}

.learn-more-link-text{
  font-size: 1.2rem;
  font-weight: 400;
}
.homepage-container{
  display: flex;
  flex-direction: column;
}

.homepage-landing-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 8% 0;
  border-bottom: 1.5px solid rgba(0,0,0,0.15);
  box-sizing: border-box;
}

.homepage-landing-image{
  width: 45%;
  padding-left: 10%;
}

.homepage-landing-title{
  font-size: 3rem;
  margin-bottom: 0.5rem;
}

.homepage-landing-title-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.homepage-landing-text-underline{
  position: relative;
  display: inline;
  background-image: linear-gradient(to right, rgb(246,228,213), rgb(246,228,213));
  background-position: 2rem 2.4rem;
  background-repeat: repeat-x;
  background-size: 10px 15px;
}

.homepage-landing-subtitle{
  font-weight: 400;
  font-size: 1rem;
  color: #282828;
}

.homepage-mission-container{
  padding: 1rem 0;
  display: flex;
  flex-direction: column;

}

.homepage-mission-title{
  width: 85%;
  font-size: 2.25rem;
  color: #282828;
}

.homepage-mission-body-container{
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 5rem 0;
}

.homepage-mission-image{
  width: 45%;
  margin-right: calc(var(--body-side-margin) * -1);
  padding-left: 10%;
}

.homepage-mission-text-underline{
  position: relative;
  display: inline;
  background-image: linear-gradient(to right, #E5E5E5, #E5E5E5);
  background-position: 0rem 0.6rem;
  background-repeat: repeat-x;
  background-size: 30px 10px;
  padding-bottom: 1rem;
  font-weight: 700;
  color: #282828;
}

.homepage-card-crop{
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.homepage-mission-body-text{
  margin-top: 2rem;
}

.homepage-card-container{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 21%;
  max-width: 400px;
  margin-top: 2rem;
}

.homepage-cards-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 1rem;
  margin-bottom: 6rem;
}

.homepage-card-image{
  width: 100%;
  margin: 1rem 0;
}

.homepage-card-title{
  font-weight: 400;
  font-size: 1rem;
  text-decoration: underline;
  margin: 0.25rem 0 0.5rem 0;
}

.homepage-card-description{
  margin-top: 0.25rem;
  font-weight: 400;
  font-size: 0.8rem;
}

@media only screen and (max-width: 428px){
  .homepage-landing-container{
    flex-direction: column;

  }

  .homepage-mission-body-container{

    flex-direction: column;
  }

  .homepage-cards-container{
    flex-direction: column;
  }
 
  .learn-more-container{
    flex-direction: column;
  }
 
  .homepage-mission-image{
    width: 100%;
    margin-right: 0;
    padding-left: 0;
  }

  .homepage-card-container{
 
    width: 100%;
   
  }

  .homepage-landing-image{
    width: 100%;
    padding-left: 0%;
  }
  }
.initiatives-container{
