}

.team-member-name-detail{
  font-size: 5rem;
  margin-bottom: 0rem;
}

.team-member-title-detail{
  font-size: 1rem;
  margin-bottom: 1.25rem;
  margin-top: 0.3rem;
}

@media only screen and (max-width: 428px){
.team-member-details-container{
  flex-direction: column;
}

.team-member-description{
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 0px;
  margin-left: 0rem;
}

.team-member-image{
  height: 100%;
  position: relative;
  right: 0rem;
  bottom: 0;
}
}
.partner-container{
    display: flex;
    flex-direction: row;
}

.partner-form-title{
    font-size: 4.5rem;
